/**
 * @module app.simple3d
*/
const exports = {};

import 'examples/simple3d.css';
import './common_dependencies.js';
/** @suppress {extraRequire} */
import olMap from 'ol/Map';

import olView from 'ol/View';
import olLayerTile from 'ol/layer/Tile';
import olSourceOSM from 'ol/source/OSM';
import ngeoOlcsOlcsModule from 'ngeo/olcs/olcsModule';
import ngeoBase from 'ngeo';
import ngeoMapModule from 'ngeo/map/module';
import ngeoOlcsManager from 'ngeo/olcs/Manager';


/** @type {!angular.Module} **/
app.module = angular.module('app', [
  ngeoBase.module.name,
  ngeoMapModule.name,
  ngeoOlcsOlcsModule.name
]);


/**
 * @constructor
 *//* @ngInject
 * @param {angular.Scope} $rootScope Root scope.
 * @param {ngeo.olcs.Service} ngeoOlcsService The service.
 */
app.MainController = function($rootScope, ngeoOlcsService) {

  /**
   * @type {ol.Map}
   * @export
   */
  this.map = new olMap({
    layers: [
      new olLayerTile({
        source: new olSourceOSM()
      })
    ],
    view: new olView({
      center: [0, 0],
      zoom: 4
    })
  });

  // TODO: detect and use different URL for DEBUG MODE
  const cesiumUrl = '../node_modules/@camptocamp/cesium/Build/Cesium/Cesium.js';

  /**
   * @export
   * @type {olcs.contrib.Manager}
   */
  this.ol3dm = new ngeoOlcsManager(cesiumUrl, $rootScope, {
    map: this.map
  });

  // Optionally, the manager can be registered into the olcs service
  ngeoOlcsService.initialize(this.ol3dm);
};

app.module.controller('MainController', app.MainController);


export default exports;



